angular.module('ppApp').controller('patentItemCtrl', patentItemCtrl);

patentItemCtrl.$inject = ['patent', 'ca','$rootScope', '$scope', '$state', '$stateParams', 'renewalRestService']

function patentItemCtrl(patent, ca ,$rootScope, $scope, $state, $stateParams, renewalRestService) {

	var vm = this;

	vm.patent = patent;
	$scope.patent = patent;
	$scope.ca = ca;

	vm.leftTabs = {
		'Details': {
			template: 'app/templates/patent/patent.patent-info.tpl.htm',
			controller: 'patentInfoCtrl as $ctrl'
		},
		'Notifications': {
            template: 'app/templates/notifications/notifications.tpl.htm',
            controller: 'notificationsCtrl as $ctrl'
		}
	}

	vm.rightTabs = {
		'Fee Breakdown': {
			template: 'app/templates/fee-breakdown/fee-breakdown.tpl.htm',
			controller: 'feeBreakDownCtrl as $ctrl'
		},
		'Color Phase Chart': {
            template: 'app/templates/colour-chart/colour-phase-chart.tpl.htm',
            controller: 'barChartCtrl as $ctrl'
		},		
		'FX Chart': {
            template: 'app/templates/fxchart/fxchart.tpl.htm',
            controller: 'fxChartCtrl as $ctrl'
		}
	}

}

angular.module('ppApp').directive('template', ['$compile', '$http', function($compile, $http) {
    return {
        restrict: 'A',
        replace: false,
        link: function($scope, element, attrs) {

            var template = attrs['template'];
            if(template!==undefined){
                // Load the template
                $http.get(template).then(function(html){
                    // Set the template
                    var e = angular.element(html.data);
                    var compiled = $compile(e);
                    element.html(e);
                    compiled($scope);
                });
            }
        }
    };
}]);

angular.module('ppApp').directive('dynController', ['$compile', '$parse',function($compile, $parse) {
  	return {
	    restrict: 'A',
	    terminal: true,
	    priority: 100000,
	    link: function(scope, elem, attrs) {
	            // Parse the scope variable
	            var name = $parse(elem.attr('dyn-controller'))(scope);

	            elem.removeAttr('dyn-controller');
	            elem.attr('ng-controller', name);

	            // Compile the element with the ng-controller attribute
	            $compile(elem)(scope)
  		}
	}
}]);